.rules{
    width: 100%;
    height: auto;
    padding-top: 50px;
    padding-bottom: 50px;
}

.rules-content{
    width: 80%;
    height: auto;
    padding: 50px;
    background-color: #dbb96e;
    font-size: 20px;
    margin-left: auto;
    margin-right: auto;
    color: #fff;
    border-radius: 12px;
    border: 10px 10px 10px #000;
}
.rules-content a{
    color: rgb(202, 38, 38);
    text-decoration: none;
}
.rules-content p{
    line-height: 50px;
}
.rules-headline{
    text-align: center;
    color: rgb(202, 38, 38);
    font-size: 45px;
    margin-top: 50px;
    font-weight: 900;
    font-style: italic;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.rules-forbidden{
    color: rgb(202, 38, 38);
    font-size: 45px;
    font-family: 'Nunito', sans-serif;
    margin-top: 50px;
    font-weight: 900;
    font-style: italic;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.rules-image{
    width: 100%;
}

.parallax__eight{
    background: url('./../../img/parallax_eight.jpg');
    background-attachment: fixed;
    background-position-y: bottom;
    background-size: cover;
    height: 400px;
}


@media only screen and (max-width: 1280px) {
    .rules-content {
        width: 80%;
        padding: 15px;
        font-size: 20px;
    }

    .rules-headline,
    .rules-forbidden {
        font-size: 35px;
        margin-top: 20px;
    }
    .parallax__eight{
        background: url('./../../img/parallax_eight.jpg');
        background-attachment: fixed;
        background-position-y: bottom;
        background-size: cover;
        height: 300px;
    }
}

@media screen and (max-width: 820px) {
    .rules-content {
      width: 100%;
      padding: 10px;
      font-size: 20px;
    }
  
    .rules-headline,
    .rules-forbidden {
      font-size: 33px;
      margin-top: 15px;
    }
  
    .parallax__eight {
      height: 200px; /* Уменьшена высота параллакса */
    }
  }