.about{
  width: 100%;
  height: auto;
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: 10px;
  padding-bottom: 10px;
}
.about__count{
  padding-top: 200px;
}


.headline{
  color: #fff;
  padding-top: 200px;
  font-size: 35px;
  font-family: 'Nunito', sans-serif;
  font-weight: 900;
  font-style: italic;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
.headline h4{
  font-size: 35px;
  font-style: italic;
}
.headline h3 span{
  color: #0306ad;
}






.parallax_first{
  background: url('./../../img/parallax_one.webp');
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  height: 900px; 
}


@media only screen and (max-width: 1280px) {
  .headline{
    color: #fff;
    padding-top: 100px;
    font-size: 35px;
    font-family: 'Nunito', sans-serif;
    font-weight: 900;
    font-style: italic;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  }
  .about{
    padding-top: 0;
  }
  .parallax_first{
    background: url('./../../img/parallax_one.webp');
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    height: 600px; 
  }
}

@media screen and (max-width: 820px) {
  .headline {
    font-size: 28px;
    padding-top: 50px;
  }

  .parallax_first {
    height: 700px;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
  }
  
}

@media screen and (max-width: 512px) {
  .headline {
    font-size: 20px;
    padding-top: 50px;
  }

  .parallax_first {
    height: 700px;
    background-attachment: fixed;
    background-position-y: bottom;
    background-position-x: calc(10, 30, 10, 10);
    
  }
}
@media screen and (max-width: 392px) {
  .headline {
    font-size: 20px;
    padding-top: 50px;
  }

  .parallax_first {
    height: 700px;
    background-attachment: fixed;
    background-position-y: bottom;
    background-position-x: calc(10, 30, 10, 10);
    
  }
}