.videos{
    width: 100%;
    height: auto;
    padding-top: 10px;
    padding-bottom: 20px;
}
.videos-content{
    display: flex;
    margin-top: 70px;
    justify-content: space-between;
}
.videos-content iframe{
    width: 450px;
    height: 255px;
}
.videos-content iframe{
    border-radius: 12px;
    box-shadow: 0px 0px 20px #2b74fc;
}
.headline_album{
    text-align: center;
    color: #2b74fc;
    font-size: 35px;
    font-family: 'Nunito', sans-serif;
    font-weight: 900;
    font-style: italic;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.mySwiper .slide img{
    position: relative;
    width: 50%;
    height: 100%;
}
.parallax__seven{
    background: url('./../../img/parallax_seven.jpg');
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    height: 400px;
}

.mySwiper_vid .slide img {
    position: relative;
    width: 50%;
    height: 100%;
}
@media only screen and (max-width: 1280px) {
    .videos-content iframe {
        max-width: 100%; /* Полная ширина для экранов 1280px и меньше */
    }

    .headline_album {
        font-size: 25px; /* Уменьшаем размер заголовка */
    }
    .videos {
        width: 100%;
        height: auto;
        padding-top: 10px;
        padding-bottom: 20px;
    }
    
    .videos-content {
        display: flex;
        flex-wrap: wrap; /* Добавлено свойство flex-wrap для переноса видео на новую строку */
        margin-top: 30px; /* Уменьшен отступ сверху */
        justify-content: space-around; /* Изменено свойство для более равномерного распределения */
    }
    
    .videos-content iframe {
        width: 100%; /* Изменено значение ширины видео для адаптации под разные экраны */
        max-width: 450px; /* Добавлено максимальное значение ширины для сохранения пропорций */
        height: 255px;
        border-radius: 12px;
        box-shadow: 0px 0px 20px #2b74fc;
        margin-bottom: 20px; /* Добавлен отступ между видео */
    }
    
    .headline_album {
        text-align: center;
        color: #2b74fc;
        font-size: 35px;
        font-family: 'Nunito', sans-serif;
        font-weight: 900;
        font-style: italic;
    }
    .mySwiper{
        width: 100%;
        height: 700px;
    }
    .mySwiper .slide img {
        position: relative;
        width: 70%;
        height: 100%;
    }
    
    .parallax__seven {
        background: url('./../../img/parallax_seven.jpg');
        background-attachment: fixed;
        background-position: center;
        background-size: cover;
        height: 300px; /* Уменьшена высота параллакса */
    }
}
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999;
  }
  
  .modal-content {
    position: relative;
    max-width: 80%;
    max-height: 80%;
    overflow: hidden;
    z-index: 999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999;
  }
  
  .modal-image {
    width: 100%;
    height: 100%;
    object-fit: contain;
    z-index: 999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999;
  }
  
  .close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: transparent;
    border: none;
    color: black;
    font-size: 16px;
    cursor: pointer;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    border: 2px solid #2b74fc;
  }
  
