.inform{
    text-align: center;
    width: 100%;
    height: auto;
    background-color: #fff;
    padding-bottom: 10px;
}
.inform__conut{
    color: #3e3f4f;
    width: 100%;
    height: auto;
}
.inform h2{
    color: #699ff5;
    font-size: 60px;
    font-family: 'Nunito', sans-serif;
    font-weight: 900;
    font-style: italic;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
.block{
    width: 100%;
    padding-top: 50px;
    height: auto;
    display: block;
    justify-content: center;
}
.block img{
    width: 60%;
    height: 500px;
    box-shadow: 5px 5px 30px 5px #000;
    border-radius: 12px;
}
.block p{
    font-size: 25px;
    width: 100%;
    color: #3f3d3d;
    font-weight: lighter;
    text-align: left;
    font-style: italic;
}

.parallax_two{
    background: url('./../../img/parallax_one.webp');
    background-attachment: fixed;
    background-position: bottom;
    background-size: cover;
    height: 400px;
}

.parallax_three{
    background: url('./../../img/parallax_tree.jpg');
    background-attachment: fixed;
    background-position: bottom;
    background-size: cover;
    height: 400px;
}


@media only screen and (max-width: 1280px) {


    
    .inform {
        text-align: center;
        width: 100%;
        height: auto;
        background-color: #fff;
        padding-bottom: 10px;
    }
    
    .inform__conut {
        color: #3e3f4f;
        width: 100%;
        height: auto;
    }
    
    .inform h2 {
        color: #699ff5;
        font-size: 40px; /* Уменьшен размер заголовка */
        font-family: 'Nunito', sans-serif;
        font-weight: 900;
        font-style: italic;
        font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    }
    
    .block {
        width: 100%;
        padding-top: 30px; /* Уменьшен отступ сверху */
        height: auto;/* Изменено свойство на flex для центрирования элементов по горизонтали */
        display: block;
        text-align: center;
        justify-content: center;
        align-items: center; /* Добавлено выравнивание элементов по вертикали */
    }
    
    .block img {
        width: 100%; /* Изменено свойство ширины для адаптации под разные экраны */
        max-width: 600px; /* Добавлено максимальное значение ширины */
        height: 100%; /* Автоматическая высота для сохранения пропорций */
        box-shadow: 0px 0px 30px 0px #000;
        border-radius: 12px;
        margin-bottom: 50px;
    }
    
    .block p {
        font-size: 20px; /* Уменьшен размер текста */
        width: 100%;
        color: #383838;
        font-weight: lighter;
        text-align: center; /* Изменено выравнивание текста */
        font-style: italic;
        margin-left: 0;
        margin-right: 0;
    }
    
    .parallax_two {
        background: url('./../../img/parallax_one.webp');
        background-attachment: fixed;
        background-position: bottom;
        background-size: cover;
        height: 300px; /* Уменьшена высота параллакса */
    }
    
    .parallax_three {
        background: url('./../../img/parallax_tree.jpg');
        background-attachment: fixed;
        background-position: bottom;
        background-size: cover;
        height: 300px; /* Уменьшена высота параллакса */
    }
}

@media screen and (max-width: 820px) {
    .inform h2 {
      font-size: 30px;
    }
  
    .block {
      padding-top: 50px;
    }
  
    .block img {
      max-width: 100%; /* Изменено на 100% для полной ширины экрана */
      height: auto;
      margin-bottom: 30px;
    }
  
    .block p {
      font-size: 18px;
    }
  
    .parallax_two,
    .parallax_three {
      height: 200px;
    }
  }

  @media screen and (max-width: 512px) {
    .inform h2 {
      font-size: 30px;
      padding-top: 10px;
    }
  
    .block {
      padding-top: 20px;
    }
  
    .block img {
      max-width: 100%; /* Изменено на 100% для полной ширины экрана */
      height: auto;
      margin-bottom: 30px;
    }
  
    .block p {
      font-size: 14px;
    }
  
    .parallax_two,
    .parallax_three {
      height: 200px;
    }
  }