.footer{
    width: 100%;
    height: auto;
    background-color: #3b3939;
    padding: 50px;
}
.footer-content{
    display: flex;
    justify-content: space-between;
    color: #fff;
}
.footer-block{
    width: 440px;
    height: auto;
    padding: 10px;
}
.footer-block h2{
    font-size: 35px;
    font-style: italic;
}

.apps img{
    width: 50px;
    height: 50px;
    margin-left: 1em;
}

@media only screen and (max-width: 1280px) {
    .footer-block {
        max-width: 100%; /* Полная ширина для экранов 1280px и меньше */
    }

    .footer-block h2 {
        font-size: 20px; /* Уменьшаем размер заголовка */
    }

    .apps img {
        width: 50px; /* Уменьшаем размер изображения */
        height: 50px;
        margin-top: 5px;
    }
}

@media only screen and (max-width: 1024px){
    .footer-block {
        max-width: 100%; /* Полная ширина для экранов 1280px и меньше */
    }

    .footer-block h2 {
        font-size: 20px; /* Уменьшаем размер заголовка */
    }
    .footer-block iframe{
        width: 250px;
        height: 250px;
    }
    .apps img {
        width: 50px; /* Уменьшаем размер изображения */
        height: 50px;
        margin-top: 5px;
    }
}

@media screen and (max-width: 820px) {
    .footer {
      padding: 30px; 
      text-align: left;
    }
  
    .footer-content { 
      width: 100%;
      
    }
  
    .footer-block {
        
        width: 400px; /* Занимает всю доступную ширину */
        margin-bottom: 20px; /* Добавлен отступ между блоками */
    }
  
    .apps img {
      width: 50px; /* Уменьшен размер изображения */
      height: 50px; /* Уменьшен размер изображения */
      margin-bottom: 10px; /* Добавлен отступ снизу */
    }
  }

  @media screen and (max-width: 512px) {
    .footer {
      padding: 30px; 
      text-align: left;
    }
  
    .footer-content { 
      width: 100%;
      display: block;
    }
  
    .footer-block {
        
        width: 300px; /* Занимает всю доступную ширину */
        margin-bottom: 20px; /* Добавлен отступ между блоками */
    }
  
    .apps img {
      width: 50px; /* Уменьшен размер изображения */
      height: 50px; /* Уменьшен размер изображения */
      margin-bottom: 10px; /* Добавлен отступ снизу */
    }
  }