html{
    scroll-behavior: smooth;
    overflow-x: hidden;
    width: 100%;
    max-width: 100%;
}
  
.headline_seconds{
    color: #ffffff;
    padding-top: 100px;
    font-size: 35px;
    font-family: 'Nunito', sans-serif;
    font-weight: 900;
    font-style: italic;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif
  }
body{
    font-family: 'Nunito', sans-serif;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    box-sizing: border-box;
    width: 100%;
    max-width: 100%;
}
.App{
    width: 100%;
    height: auto;
}
.container{
    max-width: 1480px;
    margin: 0 auto;
    padding: 0 30px;
}

@media only screen and (max-width: 1280px) {
    .headline_seconds{
        color: #ffffff;
        padding-top: 50px;
        font-size: 30px;
        font-family: 'Nunito', sans-serif;
        font-weight: 900;
        font-style: italic;
        font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
      }
}

@media screen and (max-width: 820px){
    .headline_seconds{
        font-size: 25px;
        padding-top: 10px;
        color: #fff;
    }
}
@media screen and (max-width: 512px){
    .headline_seconds{
        font-size: 15px;
        padding-top: 30px;
    }
    .parallax__seven,
    .parallax__first,
    .parallax_two,
    .parallax_tree,
    .parallax__six,
    .parallax_four,
    .parallax_five,
    .parallax__eight,
    .parallax{
        background-attachment: fixed;
        background-position-x: right;
        background-position-y: center;
        background-size: cover;
    }
    .modal-overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.7);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999;
      }
      
      .modal-content {
        position: relative;
        max-width: 80%;
        max-height: 80%;
        overflow: hidden;
        z-index: 999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999;
      }
      .mySwiper{
        width: 100%;
        height: 300px;
      }
      .modal-image {
        width: 100%;
        height: 100%;
        object-fit: contain;
        z-index: 999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999;
      }
      
      .close-button {
        position: absolute;
        top: 10px;
        right: 10px;
        background-color: transparent;
        border: none;
        color: white;
        font-size: 16px;
        cursor: pointer;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        border: 2px solid #2b74fc;
      }
      
}


